<template>
	<view class="auto-login">
		<view class="content flex flex-column a-center j-center">
			<view class="title">{{status==='userInfo'?'授权登陆':'授权地址位置'}}</view>
			<view class="info-msg">
				请授权头像等信息，以便我们为您提供更好的服务
			</view>
			<button open-type="getUserInfo" @click="getUserProfile()" class="btn" :loading="loading"
				:disabled="loading">
				微信一键登录
			</button>
		</view>
	</view>
</template>

<script>
	import {
		getUserInfo,
	} from '@/common/js/tool';
	export default {
		data() {
			return {
				loading: false,
				code: ''
			}
		},
		onLoad() {
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					this.code = loginRes.code;

				},
				fail: function(result) {}
			});
		},
		methods: {
			getUserProfile(e) {
				let _this = this;
				uni.getUserProfile({
					desc: '获取您的头像信息',
					success(result) {
						let data = {
							code: _this.code,
							signature: result.signature,
							encrypted_data: result.encryptedData,
							iv: result.iv,
							userInfo: result.userInfo
						}
						uni.setStorageSync('user_info',data.userInfo)
					}
				})
			},
			handler() {},
		}
	}
</script>

<style scoped lang="less">
	.auto-login {
		background: rgba(0, 0, 0, 0.6);
		z-index: 99999;
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		display: flex;
		justify-content: center;
		align-items: center;

		.content {
			width: 500rpx;
			height: 600rpx;
			background: white;
			border-radius: 8rpx;

			.title {
				font-size: 34rpx;
				margin-bottom: 80rpx;
				font-weight: bold;
				width: 100%;
				text-align: center;
				color: black;
			}

			.info-msg {
				font-size: 28rpx;
				margin-bottom: 60rpx;
				padding: 0 60rpx;
				color: grey;
				text-indent: 2em;
			}

			.btn {
				padding: 8rpx 0rpx;
				width: 400rpx;
				text-align: center;
				background: #FF8D1A;
				border-radius: 8rpx;
				font-size: 26rpx;
				color: white;
			}
		}
	}
</style>
